  const [budgetDisable, setbudgetDisable] = useState(true)
  const [isFailed, setIsFailed] = React.useState(false)
  const [open, setOpen] = React.useState(false)

  const onChangeHandler = e => {
    const value = e.target.value
    if (e.target.name === "help") {
      const value = helpOptions.filter(item => item.name === e.target.value)
      setbudgetDisable(value[0].budget)
    }
    setState({
      ...state,
      [e.target.name]: value,
    })
    setIsFailed(false)
  }

  const defaultValue = type => {
    if (type === "ProductStrategy") {
      let value = "Product Strategy"
      return value
    } else if (type === "ProductDesign") {
      let value = "Product Design"
      return value
    } else if (type === "CustomSoftware") {
      let value = "Custom Software Development"
      return value
    } else if (type === "DataEng") {
      let value = "Data Engineering"
      return value
    }
  }
