import React, { useState } from "react"
import "./Card.scss"
import { navigate } from "gatsby"
import { Paper } from "@mui/material"

export const SingleCard = ({ card, col, gap }) => {
  const [hover, setHover] = useState(false)
  const { Icon, nav } = card

  const handleNavigate = () => {
    if (nav && typeof nav === "string") {
      navigate(nav)
    }
  }

  return (
    <div
      className={`col-md-${col} g-${gap}`}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      onClick={handleNavigate}
    >
      <Paper className="card card-hover" elevation={0}>
        <div className="text-center icon-div">
          <div
            className={`icon-wrapper  ${
              hover ? "bg-icon-light" : "bg-icon-dark"
            }`}
          >
            {Icon && <Icon fill={hover && "#183266"} />}
          </div>
        </div>
        <div className="heading">{card.header}</div>
        <div className="text">{card.text}</div>
      </Paper>
    </div>
  )
}

SingleCard.defaultProps = {
  col: 6,
  gap: 2,
}

function Card(props) {
  const { cardData, col, gap } = props

  return (
    <div className={`row g-5 cards`}>
      {cardData.map((card, i) => {
        return <SingleCard card={card} col={col} gap={gap} />
      })}
    </div>
  )
}

export default Card
